<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block ">
            <div class="modal-dialog modal-dialog-centered ui-draggable">
                <div class="modal-content">
                    <div class="modal-header ui-draggable-handle">
                        <h4 name="header" class="modal-title">
                            {{ $t('Choose_Destination_Folder') }}
                        </h4>
                        <button @click="closepopuptag" class="close"><em aria-hidden="true" class="fa fa-times"></em>
                        </button>
                    </div>
                    <div class="modal-body">
                        <select class="form-control col-3" @change="GetTree($event)" v-if="AdditionalStorageId!=null && AdditionalStorageId>0">
                            <option value="">{{$t('Select')}}</option>
                            <option value="Default">{{$t('DefaultStorage')}}</option>
                            <option v-for="item in AdditionalStorage" :value="item.Id">{{item.Name}}</option>
                        </select><br>
                        <div id="main" class="collapse show px-1 py-1 border FullTreeData"
                            style="max-height:90vh; overflow-y: auto;">
                            <DocCommonTreeView :key="RefreshDocCommon" :checkActionType="checkActionType" treeId="MoveTree" :dynamicLeftMenu="dynamicLeftMenu" :AdditionalStorageId="AdditionalStorageId" @Folderlisting="Folderlisting" :mySpaceOrOrganization="mySpaceOrOrganization" :organizationSpace="organizationSpace"  :type="checkType" :MySpaceFolders="FolderArray" @FolderIdMoveLeftMenu="FolderIdMoveLeftMenu" :FolderIdMoveLeftMenu="FolderIdMoveLeftMenu"></DocCommonTreeView>                          
                        </div>
                    </div>
                    <div class="modal-footer text-right">
                        <button title="Search" @click="MoveFolderFiles" v-if="checkActionType == 'Move'"
                            class="popupgreenbtn btn btn-success formbtn widthhalf"><em class="fa fa-arrows"></em>
                            {{$t('Move')}}
                        </button>
                        <button title="Search" class="popupgreenbtn btn btn-success formbtn widthhalf"
                            @click="CloneFolderFiles" v-if="checkActionType == 'Copy'"><em class="fa fa-files-o"></em>
                            {{$t('Clone')}}
                        </button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DocCommonTreeView from "../../../components/DocCommonTreeView.vue";
import DataService from "./../../../services/DataService";
import ChildFolder from "./.././../../components/ChildFolder.vue"
export default {
    components: {
    ChildFolder,
    DocCommonTreeView
},
    props: {
        // FolderIdMoveLeftMenu: {
        //     type: Function,
        //     required: true
        // },
        defaultData:{
            type: Array,
            required: false,
        },
        checkType:{
            type:String
        },
        checkActionType: {
            type: String,
            required: false
        },
        moveObj: {
            type: Array,
            required: true,
        },
        organizationSpace:{
            type: Array,
            required: true,
        },
        FolderArray:{
            type: Array,
            required: true,
        },
        mySpaceOrOrganization:{
            type: Number,
            required: false
        },
        AdditionalStorageId:{
            type: String,
            required: false
        },
        dynamicLeftMenu:{
            type: Array,
            required: false,
        }
    },
    data() {
        return {

            isLoading: false,
            loggeduserid: null,
            //folderarray: [],
            FindalFolderArray: [],
            AdditionalStorage:[],
            checkAdditionalOrDefaultMove:'',
            additionalChangesId:0,
            RefreshDocCommon:0
            // checkType:'popup'
        }
    },
    mounted: function () {
        console.log($("#FolderFullTree"))
        $("#FolderFullTree").last().treeview();
        $("body").on("click", "#FolderFullTree .folderlink", function () {
            $("#FolderFullTree .folderlink").removeClass("selectedfolder");
            $(this).addClass("selectedfolder");
        });
        this.RefreshDocCommon+=1;
    },
    created: function () {
        this.loggeduserid = this.GetUserInfo.ID;
        this.GetFullTree();
        this.GetAdditionalStorage();
        this.RefreshDocCommon+=1;
    },
    methods: {
        Folderlisting:function(data, parentid, actionVal, name,routeListingcode,driveid,additionalFolderId){
            var vm=this;
           vm.$emit('Folderlisting',data,additionalFolderId,'root');
        },
        GetTree:function(e){
            var vm=this;
            var id=e.target.value;
            vm.additionalChangesId=id;
            vm.FolderArray=[];
            vm.checkAdditionalOrDefaultMove=e.target.value;
            vm.organizationSpace=[];
            if(id=="Default"){
                if(vm.mySpaceOrOrganization==1){
                    vm.organizationSpace=vm.defaultData;
                }
                else{
                    vm.FolderArray=vm.defaultData;
                }
            }
            else{
                if(vm.mySpaceOrOrganization==1){
                    vm.GetOrganizationSpace(id);
                }
                else{
                    vm.GetAdditionalFullTree(id);
                }
            }
        },
        GetAdditionalFullTree(id) {
            var vm = this;
            vm.FolderArray=[];
            DataService.FolderFullTreeView().then((response) => {
                response.data.folderTreeList.forEach((value, index) => {
                    if (value.OWNER_USER_ID == vm.loggeduserid && value.IsGlobal == 0 && value.additional_storage_id!=null) {
                        if (value.FOLDER_PARENT_ID == null && parseInt(value.additional_storage_id)==parseInt(id)) {
                            let folderId = value.FOLDER_ID
                            let rslt = vm.getsharedChildFolder(folderId, value, vm.folderTreeList)
                            vm.FolderArray.push(rslt)
                            vm.RefreshDocCommon++;
                        }
                    }
                });
            });
        },
        GetOrganizationSpace: function (id) {

            var vm = this;
            vm.organizationSpace=[]
            vm.additionalOrganizationSpace=[];
            vm.dynamicLeftMenu=[];
            DataService.GetOrganizationFolderTreeData().then((response) => {
                response.data.folderTreeList.forEach((value, index) => {
                    var CheckIfExist = vm.organizationSpace.filter(function (el) {
                        return el.FOLDER_ID == value.FOLDER_ID;
                    });
                    if (CheckIfExist.length == 0) {
                         if(parseInt(value.additional_storage_id)==parseInt(id) && value.IsGlobal!=-1){
                            if (value.FOLDER_PARENT_ID == null) {
                                let folderId = value.FOLDER_ID
                                let rslt = vm.getsharedChildFolder(folderId, value, vm.folderTreeList);
                                vm.organizationSpace.push(rslt)
                            }
                        }
                    }
                    if(value.IsGlobal==-1 && parseInt(value.additional_storage_id)==parseInt(id)){
                        vm.dynamicLeftMenu.push(value)
                    }
                });
            });
        },
        getsharedChildFolder: function (folderId, currentItem, data) {
            
            let ths = this;
            let retrunArray = currentItem
            var childArray = data.filter(function (el) {
                return el.FOLDER_PARENT_ID == folderId;
            });
            let finalChildArray = [];
            childArray.forEach((value, index) => {
                let ChildfolderId = value.FOLDER_ID
                ths.str+=ChildfolderId +',';
                var CheckIfExist1 = finalChildArray.filter(function (el) {
                    return el.FOLDER_ID == value.FOLDER_PARENT_ID;
                });
                if (CheckIfExist1.length == 0) {
                    var CheckIfExist = data.filter(function (el) {
                        return el.FOLDER_PARENT_ID == ChildfolderId;
                    });
                    if (CheckIfExist != null && CheckIfExist.length > 0) {
                        let rslt = ths.getsharedChildFolder(ChildfolderId, value, data);
                        finalChildArray.push(rslt);
                    } else {
                        finalChildArray.push(value);
                    }
                }
            });
            retrunArray.chidFolder = finalChildArray;
            return retrunArray
        },
        GetAdditionalStorage:async function(){
            var vm=this;
            await DataService.GetAdditionalStorage().then((response) => {
                vm.AdditionalStorage=JSON.parse(response.data);
            })
        },
        GetFullTree() {            
            var vm = this;
            vm.isLoading=true;
            var params = JSON.stringify(vm.moveObj);
            DataService.MoveFolderTreeView(params).then((response) => {
                response.data.folderTreeList.forEach((value, index) => {
                    //vm.sharedfolders(value)
                    if (value.OWNER_USER_ID == this.loggeduserid && value.IsGlobal == 0) {
                        var CheckIfExist = vm.FindalFolderArray.filter(function (el) {
                            return el.FOLDER_ID == value.FOLDER_ID;
                        });


                        if (CheckIfExist.length == 0) {
                            if (value.FOLDER_PARENT_ID == null) {
                                let folderId = value.FOLDER_ID
                                let rslt = vm.getChildFolder(folderId, value, response.data.folderTreeList)
                                vm.FindalFolderArray.push(rslt)
                            }
                        }

                    }

                });
                vm.folderTreeList = response.data.folderTreeList;
                vm.isLoading = false;
            });
        },
        getChildFolder: function (folderId, currentItem, data) {
            let ths = this;
            let retrunArray = currentItem
            var childArray = data.filter(function (el) {
                return el.FOLDER_PARENT_ID == folderId;
            });
            let finalChildArray = [];
            childArray.forEach((value, index) => {
                let ChildfolderId = value.FOLDER_ID
                var CheckIfExist1 = finalChildArray.filter(function (el) {
                    return el.FOLDER_ID == value.FOLDER_ID;
                });
                if (CheckIfExist1.length == 0) {
                    var CheckIfExist = data.filter(function (el) {
                        return el.FOLDER_PARENT_ID == ChildfolderId;
                    });
                    if (CheckIfExist != null && CheckIfExist.length > 0) {
                        let rslt = ths.getChildFolder(ChildfolderId, value, data);
                        finalChildArray.push(rslt);
                    } else {
                        finalChildArray.push(value);
                    }
                }
            });
            retrunArray.chidFolder = finalChildArray;
            return retrunArray
        },
        FolderIdMoveLeftMenu(data, parentid,actionVal,name,aid,additionalStorageFolderId) {
            debugger
            var vm = this;
            vm.FolderIds = data;
            vm.$emit('FolderIdmove', data, parentid,name,aid,additionalStorageFolderId);
            vm.$emit("GetAdditionalFullTree",this.FolderArray,this.checkAdditionalOrDefaultMove);
        },
        MoveFolderFiles: function () { 
            var vm = this;
            vm.$emit("MoveFoldersFiles",this.checkAdditionalOrDefaultMove);
        },
        CloneFolderFiles: function () {

            var vm = this;
            vm.$emit("CloneFoldersFiles",this.checkAdditionalOrDefaultMove);
            vm.$parent.closemovefilefolderpopup();
        },
        closepopuptag: function () {

            var vm = this;
            vm.$emit("closemovefilefolderpopup");
            vm.$parent.closepopupFolder();
        }
    }
}
</script>